export const InputTag = (props) =>{
    return (<>
        {(props.divi)?
        <div className="mb-3 col-sm-6">
            <label className="form-label">{props.Title}</label>
            <input ref={props.link} type="text" className="form-control"/>
        </div>
        :
        <div className="mb-3">
            <label className="form-label">{props.Title}</label>
            <input ref={props.link} type="text" className="form-control"/>
        </div>
        }
    </>)
}