import { useRoutes } from "react-router-dom";
import { AddMovie } from "../../task/crud/pages/AddMovie";
import { AddSeries } from "../../task/crud/pages/AddSeries";
import { EditMovie } from "../../task/crud/pages/EditMovie";
import { EditSeries } from "../../task/crud/pages/EditSeries";
import { EditSnapShots } from "../../task/crud/pages/EditSnapShots";
import { SearchMovie } from "../../task/crud/pages/SearchMovie";
import { Summary } from "../../task/crud/pages/Summary";

export const AppRoutes = () => {
    const allRoutes = useRoutes([
        {path:'/',element:<Summary/>},
        {path:'/searchMovie', element:<SearchMovie/>},
        {path:'/add_new_movie', element:<AddMovie/>},
        {path:'/add_new_series',element:<AddSeries/>},
        {path:'/edit_movie_details', element:<EditMovie/>},
        {path:'/edit_series_details', element:<EditSeries/>},
        {path:'/edit_snapshots_movie',element:<EditSnapShots/>}
    ])
    return allRoutes
}