import * as React from 'react';
import { DataGrid } from '@mui/x-data-grid';
import axios from 'axios';
import { TaskContext } from '../../provider/TaskContext';

const columns =  [
    { field: 'id', headerName: 'Movie Id', width: 130 },
    { field: 'movie_name', headerName: 'Movie Name', width: 300 },
    { field: 'release_date', headerName: 'Release Date', width: 130 },
    { field: 'movie_poster', headerName: 'Movie Poster', width: 500 },
  ];

export default function DataTable(props) {
  const context = React.useContext(TaskContext)

    async function searchFromIMDB(id){
        async function detail(){
            const imdb =  await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=520e0927a4eacf210a53d132ce8cb28c&language=en-US`)
            return imdb.data
        }
        const imdb_detail = await detail()
        context.tasks.push({imdbID:imdb_detail.imdb_id,
                            tmdbID:id})
        // console.log(imdb_detail.imdb_id)
        // const tmdbClean = tmdb_detail.map(e=>({'id':e.id,'movie_name':e.title,'release_date':e.release_date,'movie_poster':`${process.env.REACT_APP_MOVIE_POSTER_URL}${e.poster_path}`}))
        // setMovieData(tmdbClean)
    }
  return (
    <div style={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={props.movieRow}
        columns={columns}
        pageSize={5}
        rowsPerPageOptions={[5]}
        checkboxSelection
        onSelectionModelChange={(ids) => {
            const selectedIDs = new Set(ids);
            // console.log(rows)
            const selectedRows = props.movieRow.filter((row) =>
              selectedIDs.has(row.id),
            );
            searchFromIMDB(selectedRows[0].id)
            // console.log(selectedRows[0].id)
          }}
      />
    </div>
  );
}