import { InputTag } from "../components/InputTag"

export const EditMovie = () =>{
    return (<>
            <h2>Edit Movie Details</h2>
                <div className="row g-4">
                    <div className="col-sm-12">
                        <div className="bg-secondary rounded h-100 p-4">
                            <h6 className="mb-4">Movie Details</h6>
                            <div className="mb-5">
                                <label  className="form-label">Search Movie From DataBase</label>
                                <div className="d-flex">
                                    <input className="form-control" type="text" placeholder="Search Movie"/>
                                    <button type="button" className="btn btn-primary ms-2">Search</button>
                                </div>
                            </div>
                            <form>
                                <InputTag Title="Title"/>
                                <InputTag Title="Movie Name with Download"/>
                                <InputTag Title="Download Link"/>
                                <div className="mb-3">
                                    <label  className="form-label">Cover Images</label>
                                    <textarea type="text" className="form-control" aria-describedby="emailHelp"></textarea>
                                </div>
                                <InputTag Title="Movie Poster"/>
                                <div className="row g-4">
                                    <div className="mb-3 col-sm-6">
                                        <label className="form-label">Language</label>
                                        <select defaultValue="Select Language" className="form-select">
                                            <option>Select Language</option>
                                            <option value="1">Dual Audio (Hindi-English)</option>
                                            <option value="2">Dual Audio (Hindi-Tamil)</option>
                                            <option value="3">Dual Audio (Hindi-Korean)</option>
                                            <option value="3">Dual Audio (Hindi-Telugu)</option>
                                            <option value="3">Dual Audio (English-Korean)</option>
                                            <option value="3">Multi Audio (Hindi-Tamil-English)</option>
                                            <option value="3">Multi Audio (Hindi-English-Spanish)</option>
                                            <option value="3">Multi Audio (Hindi-English-Korean)</option>
                                            <option value="3">English</option>
                                            <option value="3">Telugu</option>
                                            <option value="3">Korean</option>
                                            <option value="3">Turkish</option>
                                            <option value="3">Spanish</option>
                                        </select>
                                    </div>
                                    <div className="mb-3 col-sm-6">
                                        <label className="form-label">Movie Size</label>
                                        <input type="password" className="form-control" id="exampleInputPassword1"/>
                                    </div>
                                </div>
                                <div className="row g-4">
                                <div className="mb-3">
                                    <label  className="form-label">Keywords</label>
                                    <input type="text" className="form-control" aria-describedby="emailHelp"/>
                                </div>
                                </div>
                                <div className="row g-4 mb-2" style={{marginLeft:"0"}}>
                                    <div className="mb-3 form-check col-sm-4">
                                        <input type="checkbox" className="form-check-input"/>
                                        <label className="form-check-label">Updated Movies</label>
                                    </div>
                                    <div className="mb-3 form-check col-sm-4">
                                        <input type="checkbox" className="form-check-input"/>
                                        <label className="form-check-label">Recommeded By Moviezwala</label>
                                    </div>
                                    <div className="mb-3 form-check col-sm-4">
                                        <input type="checkbox" className="form-check-input"/>
                                        <label className="form-check-label">New Release</label>
                                    </div>
                                </div>
                                <button type="submit" className="btn btn-primary">Add Movie</button>
                            </form>
                        </div>
                    </div>  
                </div>
    </>)
}